html, body {
  
  
  background-color: #1ED5D5 !important;
  height: 100%;
  width: 100%;
  font-family: 'Roboto';
  margin: 0;
  padding: 0;

}

#root {

  height: 100%;
  width: 100%;

}

.MuiInput-underline:before {

  border-bottom-color: #BEBEBE !important;

}

.MuiInput-underline:after {

  border-bottom-color: #3D2892 !important;

}

label {

  color:#BEBEBE !important;
  font-weight: bold !important;
  font-size: 1.4rem !important;


  &.Mui-focused {

    color:#3D2892 !important;

  }

}




@keyframes fadeIn {

  from {

      opacity: 0%;

  } to {

      opacity: 100%;

  }

}