.home-container {

    display:flex;
    justify-content: center;
    align-items: center; 
    flex-direction: column;
    height:  100%;
    padding: 0.45rem;
    animation: fadeIn 1s ease-in;
   
    .months-container {

        width: 100%;
        display:flex;
        justify-content: center;
        align-items: center; 

        .months {

            width: 90%;
    
            column-gap: 0.15rem;
            row-gap: 0.5rem;
    
            display:grid;
            grid-template-columns: repeat(auto-fit, minmax(216px, auto));
            justify-items: center;
    
        }

    }

}

