.month-box {

    width: 216px;
    height: 188px;

    background-color: #fff;
    border-radius: 8px;

    box-shadow: 0px 3px 6px 0px rgba(0,0,0,0.16);

    display: flex;
    align-items: center;
    justify-content: center;

    h5 {

        font-size: 2rem;
        color: #1ED5D5;

    }

    &.available:hover, &.warning:hover {

        cursor:pointer;

    }

    &.available, &.unavailable {

        h5 {

            margin-bottom: 10.5rem;

        }

    }

    &.unavailable {

        opacity: 70%;

        &:hover {

            cursor: not-allowed;

        }

    }

    &.ok, &.warning {

        flex-direction: column;

        h5 {

            margin: 0;
            margin-top: -3rem;

        }

    }

    &.ok {

        
        color:#3D2892;
        background-color: #69FFBC;

        h5 {

            color:#3D2892;
            margin-top: 0;

        }

        .check-icon {

            margin-bottom: 0rem;
            margin-top: 2.75rem;

        }

    }

    &.warning {

        color:#FF9148;

        h5 {

            color: #FF9148;
            margin-top: 0;
            position: relative;
            top: 0.75rem;

        }

        .warning-icon {

            margin-bottom: 0rem;
            margin-top: 3rem;

            h4 {

                margin: 0;
                font-size: 4rem;

            }

        }


    }


}
