.appointment-container {

    display:flex;
    justify-content: center;
    align-items: center; 
    flex-direction: column;
    height:  100%;
    padding: 0.45rem;
    animation: fadeIn 1s ease-in;

    .appointment-box {

        display:flex;
        justify-content: center;
        align-items: center; 
        flex-direction: column;

        background-color: #fff;
        border-radius: 8px;

        box-shadow: 0px 3px 6px 0px rgba(0,0,0,0.16);

        width: 90%;

        padding:1rem;

        h4 {

            color:#3D2892;
            font-size: 2rem;

        }

        .inputs {

            width:100%;
            display: flex;
            flex-direction: row;
            justify-content: center;

            .input-container {

                width: 40%;
                margin: 2rem;
            
                .text-input {
            
                    width: 100%;
                    
                    label {
                
                        font-weight: bold;
                        font-size: 1.4rem;
                
                    }
                
                }
            
            }

        }

        .button-container {

            width: 90%;
            margin: 0.5rem 0;

        }


    }

}

@media (max-width:600px){

    .appointment-container {
    
        .appointment-box {
    
    
            .inputs {
    
                flex-direction: column;
    
                .input-container {
    
                    width: 80%;
                
                }
    
            }
    
    
        }
    
    }

}