.signup-container {

    display:flex;
    justify-content: center;
    align-items: center; 
    flex-direction: column;
    height:  100%;
    padding: 0.45rem;
    animation: fadeIn 1s ease-in;
   

}

.signup-box {

    border-radius: 8px;
    background-color: #fff;
    min-width: 360px;
    
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    padding: 1.25rem 0.45rem;

    box-shadow: 0px 3px 6px 0px rgba(0,0,0,0.16);


}

.input-container {

    width: 80%;
    margin: 2rem 0;

    .text-input {

        width: 100%;
        
        label {
    
            font-weight: bold;
            font-size: 1.4rem;
    
        }
    
    }

}

.radio-container {

    display: flex;
    justify-content: space-between;


}

.primary-button {

    width:100%;

}



form {

    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;

}

h1 {

    color:#fff;
    font-size: 2.5rem;
    text-align: center;

}