.consultings-container {

    display:flex;
    justify-content: center;
    align-items: center; 
    flex-direction: column;
    height:  100%;
    padding: 0.45rem;
    animation: fadeIn 1s ease-in;
   
    .consultings {

        width: 90%;

    }

    .input-container {

        width: 90%;
        margin: 2rem;
    
        .text-input {
    
            width: 100%;
            
            label {
        
                font-weight: bold;
                font-size: 1.4rem;
        
            }
        
        }
    
    }

    .button-container {

        width: 90%;
        margin: 0.5rem 0;

    }

}



