.form {

    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    background-color: #fff;
    border-radius: 0.5rem;
    box-shadow: 0px 3px 6px 0px rgba(0,0,0,0.16);

    margin: 0.5rem 0;

}